import React, { Component,PropTypes } from 'react'
import { MdMessage } from "react-icons/md";
import { MdNotificationsNone } from "react-icons/md";
import { AiFillQuestionCircle } from "react-icons/ai";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { IoReorderThreeOutline } from "react-icons/io5";
import { IoPeopleOutline } from "react-icons/io5";
import RichTextEditor from 'react-rte';

class Page extends Component {
   
    constructor(props) {
        super(props);
    
        this.state = {
            value:'',
            fileInput:'',
            startDate:'',
            endDate:'',
            starttime:'',
            endtime:'',
            discriptionbox:''
        }
        
    }
    handleChange=(event)=>{
        this.setState({
            [event.target.name]:event.target.value
        })
    }

    submitHandler=(event)=>{
         /* event.preventDefault(); */ 
        alert('Event is at '  +this.state.value+" "+this.state.fileInput+" "+this.state.startDate+" "+this.state.endDate+" "+this.state.starttime
        +" "+this.state.endtime); 
    }

    upload(event)
    {
        this.setState({
            fileInput:event.target.files[0].name
        })
    }
    StartdateHandler=(event)=>{
        this.setState({
            startDate:event
        })
    }
    endDateHandler=(event)=>{
        this.setState({
            endDate:event
        })
    }

    render() {
        return (
            <div className='container'>
                
                <div className="row" style={{marginInlineStart:' 564px',marginRight:'164px'}}>
                    <div className="col sm-12"><IoReorderThreeOutline/></div>
                    <div className="col s1"><IoPeopleOutline/></div>
                    <div className="col s1"><MdNotificationsNone/></div>
                    <div className="col s1"><MdMessage color='black'/></div>
                </div>

                <div style={{float:'right'}}><button className="btn btn-primary">Create</button>&nbsp;&nbsp;&nbsp;
                <button className="btn btn-danger">Cancel</button></div>

                <form autoComplete='off' onSubmit={this.submitHandler}>

                <input className='col-md-5 form-group container' placeholder='Search' type='search'/>
                
                    <div className='col-md-8 form-group container'>
                    <label className='float-left block-text text-darken-2 s'>
                    Add Event in*
                    </label>
                <select value={this.state.value} name="value" onChange={this.handleChange} className='form-control' required>
                    <option value=''>Select...</option>
                    <option value='10am'>10am</option>
                    <option value='12pm'>12am</option>
                    <option value='2pm'>2pm</option>
                    <option value='4pm'>4pm</option>
                </select>
                    </div>

                    <div className='col-md-8 form-group container'>
                        <label className='float-left block-text text-darken-2 s'>Title*</label>
                        <input type='text' name='Title' value='' onChange='' className='form-control' required />
                    </div>

                    <div className='col-md-8 form-group container'>
                    <label className='float-left block-text text-darken-2 s'>
                    Categories*
                    </label>
                <select value={this.state.option} onChange={this.handleChange} className='form-control' required>
                    <option option=''>Select...</option>
                    <option option='A'>A</option>
                    <option option='B'>B</option>
                    <option option='C'>C</option>
                    <option option='C'>C</option>
                </select>
                    </div>

                    <div className='col-md-8 form-group container'>
                        <label className='float-left block-text text-darken-2 s'>Short Summary*</label>
                        <input type='text' name='Title' value='' onChange='' className='form-control' required />
                    </div>
                    <div style={{textAlign:'initial',marginLeft:'169px'}}><strong><pre>Type:  Public</pre></strong></div>

                    <div className='col-md-8 form-group container'>
                     <input type='text' name='Title' value='' onChange='' placeholder='Registration Site' className='form-control' required />
                     </div>

                    <div className="switch" style={{textAlign:'initial',marginLeft:'169px'}}>
                     <pre><label>
                      <strong style={{color:'black'}}>Is this a virtual event?   Yes</strong>   
                     <input type="checkbox" checked/>
                     <span className="lever"></span>
                     </label></pre>
                     </div>

                     <div className='col-md-8 form-group container'>
                     <input type='text' name='Title' value='' onChange='' placeholder='Online Link' className='form-control' required />
                     </div>

                    <div className='col-md-8 form-group container'>
                    <div className="row">
                        <div className='col-sm-8'>
                        <label className='float-left block-text text-darken-2 s'>
                    Select Timezone*
                    </label>
                    <input type='text' name='Title' value='Pune' onChange='' placeholder='Online Link' className='form-control' required />
                    </div>
                        <div className="col-sm-3"><br/><br/>
                        <pre><strong>GMT+05:30<AiFillQuestionCircle/></strong></pre>
                        </div>
                    </div>
                     </div >

                    <div className='col-md-8 form-group container'>
                        <div className="row">
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            Start Date*
                            </label>
                            <DatePicker selected={this.state.startDate} onChange={this.StartdateHandler}  placeholderText="Start date" />
                    </div>
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            Start Time*
                            </label>
                             <input type='time' name='starttime' value={this.state.starttime} onChange={this.handleChange}/>
                    </div>
                        </div>
                    </div>

                    <div className='col-md-8 form-group container'>
                        <div className="row">
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            End Date*
                            </label>
                            <DatePicker selected={this.state.endDate}  onChange={this.endDateHandler}  placeholderText="End date" />
                    </div>
                            <div className="col-sm-4">
                            <label className='float-left block-text text-darken-2 s'>
                            End Time*
                            </label>
                             <input type='time' name='endtime' value={this.state.endtime} onChange={this.handleChange} />
                    </div>
                        </div>
                    </div>

                    <RichTextEditor style={{height:'200%'}}
                      value={RichTextEditor.createEmptyValue()}>
                    </RichTextEditor>
                    

                    <label>
                    Upload Image*:
                <input type='file' name='fileInput' onChange={(e)=>this.upload(e)}/>
                </label><br/><br/>
                <input type='submit' value='Submit'/>

                </form>
            </div>
        )
    }
}
export default Page